    html,
    body {
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        * {
            box-sizing: inherit;
        }
    }
    
    img {
        max-width: 100%;
        height: auto;
        object-fit: cover;
    }
    
    .d-flex {
        display: flex;
    }
    
    .header_left {
        flex: 2;
    }
    
    .header_right {
        flex: 1;
        text-align: right;
    }
    
    input::placeholder {
        text-indent: 20px;
    }
    
    select {
        text-indent: 20px;
    }
    
    .pa-100 {
        padding: 100px 0;
    }
    
    input[type="text"],
    select {
        border: transparent;
        background: #fff !important;
        color: #4d4d4d !important;
        border: 2px solid rgba(0, 0, 0, 0.1);
        font-size: 14px;
        height: 60px !important;
        border: 1px solid transparent;
        -webkit-border-radius: 0;
        -moz-border-radius: 0;
        -ms-border-radius: 0;
        border-radius: 0;
        display: inline-block;
        width: 100%;
        margin-bottom: 10px;
    }
    /* COMMEN STYLE */
    
    .pa-100 {
        padding: 100px 0;
    }
    
    .feature-header {
        .subtitle {
            text-transform: capitalize;
            font-family: "Noto Sans", sans-serif;
            color: #ccc;
            font-weight: 100;
        }
    }
    
    .feature-header {
        .title {
            font-weight: 300;
            font-family: Aril;
            text-transform: capitalize;
            margin: 11px 0 42px 0;
        }
        span {
            font-weight: bold;
            text-transform: capitalize;
        }
    }
    
    // Custom Style For All packages in Sections
    .package_detials {
        padding: 20px 20px 0px 20px;
        h3 {
            font-size: 20px;
        }
        i {
            color: rgb(163, 66, 28);
            font-size: 10px;
        }
        p {
            font-size: 14px;
            line-height: 1.7;
            margin-bottom: 5px;
        }
        span {
            color: #777;
            font-size: 14px;
        }
        .packages_body {
            margin-top: 10px;
            &::after {
                content: "";
                border-bottom: 1px solid #ccc;
                display: block;
                padding-top: 10px;
            }
        }
        .packages_footer {
            padding: 15px 0;
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-wrap: wrap;
            i,
            .packages_footer a {
                color: #777;
                text-decoration: none;
            }
            .link {
                background: #9acb62;
                text-decoration: none;
                padding: 10px;
                border-radius: 5px;
                color: #fff;
                font-size: 14px;
            }
        }
    }

    ul{
        padding: 0;
    }