/*==========  Blog Section =======================*/

.blog {
    background: #f8faff;
    .blog_wraper {
        display: grid;
        gap: 2rem;

        .blog_item {
 
            border: 1px solid #f8f8f8;
            box-shadow: rgba(77, 105, 128, 0.1) 0px 4px 12px;
            border: 1px solid #f8f8f8;
            background: #fff;
            img {
                min-height: 260px;
                max-width: 100%;
            }
        }
        @include respond-to("tablet") { 
            grid-template-columns: repeat(2,1fr);
         }
        @include respond-to("desktop") { 
            grid-template-columns: repeat(3,1fr);
         }
        @include respond-to("LGdesktop") { 
            grid-template-columns: repeat(4,1fr);
         }
    }
}