/*===================== fact =======================*/
.fact {
    background: right top linear-gradient(#3cc0c0ad, #5ceec0dc), url('../../images/background.webp');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    padding: 100px 0;
    position: relative;
    text-align: center;
    padding: 174px 0;
    .fact_header {
        color: #fff;
    }
    .fact_wraper {
        display: grid;
        gap: 1rem;
       
        .item {
           
            color: #fff;
            h4 {
              
                font-weight: bold;
            }
            p {
                font-weight: lighter;
                color: #edf2ff;
                letter-spacing: 1.2px;
            }
        }
        @include respond-to("tablet") { 
            grid-template-columns: repeat(2,1fr);
         }
        @include respond-to("desktop") { 
            grid-template-columns: repeat(4,1fr);
         }
        @include respond-to("LGdesktop") { 
            grid-template-columns: repeat(4,1fr);
         }
    }
}