/*==========  Frature Section =======================*/
.feature {
    .feature_wraper {
        display: grid;
        gap: 1rem;
        .feature_item {
            height: 350px;
            border: 1px solid #eee;
            min-height: 450px;

            .feature__text{
                h4 {
                    line-height: 1.5;
                    padding: 9px 14px 0px;
                    margin-bottom: 10px;
                    color: #304f61;
                }
                span {
                    color: #777;
                    padding: 0 15px;
                    display: inline-block;
                }  
            }
            
            img {
                width: 100%;
                height: 300px;
                background-size: contain;
            }
        }
        @include respond-to("tablet") { 
            grid-template-columns: repeat(2,1fr);
         }
        @include respond-to("desktop") { 
            grid-template-columns: repeat(4,1fr);
         }
        @include respond-to("LGdesktop") { 
            grid-template-columns: repeat(4,1fr);
         }
    }
}