// Footer Styles
/*==========  Footer Section =======================*/

.footer {
    background: #222831;
    color: #fff;
    .footer__wraper {
        display: grid;
  
 
        .footer_item {
      
            .footer__content {
                padding: 30px 0 30px 0;
                line-height: 150%;
                p {
                    color: #a6aab1;
                    font-size: 16px;
                    margin-bottom: 25px;
                }
                i {
                    color: #fff;
                    font-size: 30px;
                }
                ul {
                    list-style: none;
                    li a {
                        color: #a6aab1;
                        text-decoration: none;
                        padding: 10px;
                        font-size: 14px;
                        display: block;
                        transition: color 0.2s ease-in-out;
                        -webkit-transition: color 0.2s ease-in-out;
                        -moz-transition: color 0.2s ease-in-out;
                        -ms-transition: color 0.2s ease-in-out;
                        -o-transition: color 0.2s ease-in-out;
                        &:hover {
                            color: #9acb62;
                        }
                        i {
                            color: #fff;
                            margin-right: 10px;
                        }
                    }
                }
            }
        }

        @include respond-to("tablet") { 
            grid-template-columns: repeat(2,1fr);
         }
        @include respond-to("desktop") { 
            grid-template-columns: repeat(4,1fr);
         }
        @include respond-to("LGdesktop") { 
            grid-template-columns: repeat(4,1fr);
         }
    }
    .copyright {
        text-align: center;
        color: #a6aab1;
        padding: 15px;
        border-top: 1px solid #40484a;
        a {
            color: #fff;
            transition: color 0.2s ease-in-out;
            -webkit-transition: color 0.2s ease-in-out;
            -moz-transition: color 0.2s ease-in-out;
            -ms-transition: color 0.2s ease-in-out;
            -o-transition: color 0.2s ease-in-out;
            color: #9acb62;
        }
        p {
            font-size: 12pt;
        }
        i {
            color: red;
            margin: 0 3px;
        }
    }
}