// Fonts
$sansSerif: "Noto Sans",
sans-serif;
$titleFont: 'camar';
// Font Sizing
$baseFontSize: 16px;
$baseLineHeight: 1.5;
$headingLineHeight: 1.1;
$f-size-200: 0.8rem;
$f-size-300: 1rem;
$f-size-400: 1.25rem;
$f-size-500: 1.563rem;
$f-size-600: 1.953rem;
$f-size-800: 2.441rem;
$f-size-900:3.052rem ;
$f-size-title:4rem ;
// Colors
$primary-color: #9acb62;
$secondary-color: #f85959;
$darkish-blue: #212d48;
$white-color: #fff;
// Spacing
$pad: 1em;
// Breakpoints for Container
$maxMobile: "(max-width:767px)";
$maxSmall: "(max-width:991px)";
$minSmall: "(min-width:768px)";
$minMedium: "(min-width:992px)";
$minLarge: "(min-width:1200px)";