/*========== header Section =======================*/
.top-header {
    padding: 1rem 0;
    position: relative;

    .navbar {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .logo {
            font-size: 1.6rem;
            flex-basis: 350px;
            font-weight: 700;
            text-decoration: none;
            text-transform: uppercase;

            span {
                color: $secondary-color;
                font-weight: 600;
                font-family: 'Source Sans Pro', sans-serif;
                text-transform: uppercase;
            }
        }

        .links {

            list-style: none;
        

            li {
                display: inline-block;

                @include respond-to("phone") {
                    display: block;
                    padding: 0.8rem;
                }

                @include respond-to("tablet") {
                    display: block;
                    padding: 0.8rem;
                }

                @include respond-to("desktop") {
                    display: inline-block;
                    padding-right: 0.2rem;
                }


                a {
                    padding: 0 1rem;
                    text-decoration: none;
                    text-transform: capitalize;
                    transition: color .2s ease-in-out;
                    @include prefix(transition, color .2s ease-in-out, webkit ms moz);


                    &:hover {
                        color: $secondary-color;
                    }
                }
            }


            @include respond-to("desktop") {
                text-align: right;
            }
        }


        .menu__icon {
            display: none;

            cursor: pointer;
            transition: rotate .2s ease-in;

            span {
                font-size: 24px;
                color: $secondary-color;

                &:hover {
                    color: $primary-color;

                }


            }



            @include respond-to("phone") {
                justify-content: flex-end;
                display: flex;

            }

            @include respond-to("tablet") {
                justify-content: flex-end;
                display: flex;


            }

            @include respond-to("desktop") {

                display: none;


            }


        }

    }
}

@media (max-width: 600px) {}

@include respond-to("phone") {
    .links {
        background: #fff;
        z-index: 999;
        position: absolute;
        left: 0;
        top: 100%;
        width: 100%;
        visibility: hidden;
        opacity: 0;

    }

    .links_active {
        visibility: visible;
        opacity: 1;

    }
}

@include respond-to("tablet") {
    .links {
        background: #fff;
        z-index: 999;
        position: absolute;
        left: 0;
        top: 100%;
        width: 100%;
        visibility: hidden;
        opacity: 0;

    }

    .links_active {
        visibility: visible;
        opacity: 1;

    }

}
@include respond-to("desktop") {
    .links {
        position: relative;
        top: 0;
        visibility: visible;
        opacity: 1;

    }

    .links_active {
        visibility: visible;
        opacity: 1;

    }

}