html {
    font-family: $sansSerif, sans-serif;
    font-weight: normal;
    color: $darkish-blue;
    line-height: $baseLineHeight;
    font-size: 14px;
  
    // @include respond-to("tablet") { 
    //     font-size: 15px;
    //  }
    // @include respond-to("desktop") { 
    //     font-size: 16px;
    //  }
    // @include respond-to("LGdesktop") { 
    //     font-size: 20px;
    //  }

}

// Header Styles
h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: bold;
    margin: 0;
    margin-bottom: $pad;
    line-height:1.8;
 
}
h1{
    line-height: $headingLineHeight;
}

p {
    margin-bottom: $pad;
}

// Text Styles
em {
    font-style: italic;
}

strong {
    font-weight: bold;
}

.uppercase {
    text-transform: uppercase;
}

h1,
h2,
h3,
h4 {
    /* font-family: 'Source Sans Pro', sans-serif; */
    font-weight: bold;
}

h1 {
    font-size:$f-size-900;
}

h2 {
    font-size: $f-size-800;
}

h3 {
    font-size: $f-size-600;
}
h4 {
    font-size: $f-size-500;
}

p {
    font-size: $f-size-300;
}
a{
    color: $darkish-blue;
}