  /*==========  Services Section =======================*/
.services {
    background: #f8faff;
    padding-bottom: 5%;

    .services_wraper {
       display: grid;
       gap: 2rem;
        position: relative;
        top: -147px;
        .services_item {
           
            background: #fff;
            padding:1rem 15px;
            text-align: center;
            margin-top: -10px;
            box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
            i {
                color: #f85959;
                padding: 1rem 0;
                font-size: 2rem;
            }
            h3 {
                font-weight: 300;
                font-family: "Noto Sans", sans-serif;
                font-size: 24px;
                width: 100%;
                margin: 10px 0;
            }
            p {
                font-size: 16px;
                margin: 15px 0;
                color: #999;
            }
        }

        @include respond-to("tablet") { 
            grid-template-columns: repeat(2,1fr);
         }
        @include respond-to("desktop") { 
            grid-template-columns: repeat(4,1fr);
         }
       
    }
}