/*==========  Hero  Section =======================*/
.hero {
    background: url(../../images/hero.png);
    height: 100vh;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
   
    @include respond-to("tablet") { 
        height:80vh;
     }

    .hero__content {
         
          
        

        .hero_left__box {
      
            @include respond-to("phone") { 
                padding-top: 3rem;
             }
            @include respond-to("tablet") { 
                @include center-y();
             }
            @include respond-to("desktop") { 
                @include center-xy();
             }
            .hero_title {
                font-family: $titleFont;
                color: $white-color;
                padding-top: 3rem;
        
                letter-spacing: 3px;
              
                margin-bottom: 0;
                @include respond-to("tablet") { 
                   
                  
                 }
                @include respond-to("desktop") { 
                 
                    width: 20ch;
                 }
            }
    
            .hero_desc {
                color: $white-color;
                font-size: 1.1rem;
               

                @include respond-to("desktop") { 
                    width: 70ch;
                 }
            }
        }

      
    }
}