/*==========  Testemonials Section =======================*/
.about {
    background: #f8faff;
    .about__wraper {
        display: grid;
        gap: 2rem;
       
  
        .about_us_body {
            p {
                line-height: 1.8;
                color: #999;
                font-weight: normal;
                font-size: 16px;
                margin-bottom: 20px;
                &:last-of-type {
                    padding-bottom: 30px;
                }
            }
            .link {
                border: 1px solid rgb(202, 78, 78);
                padding: 15px 20px;
                border-radius: 26px;
                text-decoration: none;
                font-weight: 400;
                color: rgb(202, 78, 78);
            }
        }
        .testemonials {
  
            .testem_body {
                background: #fff;
                display: flex;
                padding: 50px;
                img {
                    width: 200px;
                    border-radius: 50%;
                }
                .img-wrap {
                    position: relative;
                }
                i {
                    background: rgb(202, 78, 78);
                    width: 40px;
                    height: 40px;
                    line-height: 40px;
                    text-align: center;
                    border-radius: 50%;
                    color: #fff;
                    position: absolute;
                    right: 3px;
                    top: 70px;
                }
                .person-info {
                    padding-left: 20px;
                    p {
                        font-size: 16px;
                        margin-bottom: 15%;
                        color: #999;
                        line-height: 1.8;
                    }
                    h4 {
                        font-size: 24px;
                        font-weight: normal;
                    }
                    span {
                        color: #999;
                        font-size: 16px;
                    }
                }
            }
        }

        @include respond-to("phone") { 
            grid-template-columns: repeat(1,1fr);
         }
         @include respond-to("tablet") { 
            grid-template-columns: repeat(2,1fr);
         }
     
       
    }
}