/*==========  Fact Section =======================*/
.fact {
    .fact_header {
        color: #fff;
        padding: 0 2rem;
        margin: auto;
        h2 {
            text-transform: capitalize;
        }
        p {
            margin: 10px 0 40px 0;
            color: #ffffff;
            font-size: 12pt;
        }
        form {
            input.formStyle {
                width: 240px;
                height: 41px;
                border: 1px solid #c8f1eb;
                background: transparent;
                padding-left: 10px;
            }
        }
    }
}